@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 13px 25px 13px 15px;
  background-color: #24252a;
}

.logo {
  cursor: pointer;
  margin-right: auto;
  width: 190px;
}

.nav-link {
  list-style: none;
  margin: 0;
}

.nav-link li {
  display: inline-block;
  padding: 0px 20px;
  font-family: Montserrat, sans-serif;
}

.nav-link li a {
  transition: all 0.3s ease 0s;
  color: white;
  text-decoration: none;
}

.nav-link li a:hover {
  color: #0088a9;
}

.login-button {
  padding: 3px 15px;
  margin-left: 10px;
  background: rgba(0, 136, 169, 1);
  border: 2px solid white;
  border-radius: 50px;
  transition: all 0.5s;
  font-size: 15px;
  font-weight: 400;
  font-family: Montserrat, sans-serif;
  color: white;
  outline: none;
}

.login-button:hover {
  color: rgba(0, 136, 169, 1);
  background-color: white;
  border: 3px solid rgba(0, 136, 169, 1);
}

.dropbtn {
  background-color: #24252a;
  color: white;
  font-size: 16px;
  border: none;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 80px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a,
button {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
  background-color: #ddd;
  text-decoration: none;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

@media screen and (max-width: 800px) {
  .login-button {
    padding: 2px 12px;
    font-size: 13px;
  }
  .nav-link li {
    display: none;
  }
  .logo {
    width: 150px;
  }
  .header {
    padding: 10px 18px 10px 10px;
  }
}

body {
  padding: 0;
  background-size: cover;
  background-image: url(/boilerplate-react/static/media/background.fe4ac80a.jpg);
  background-position: 0 0;
  background-position: initial;
  font-family: sans-serif;
}

.loginbox {
  margin-top: 50px;
  width: 420px;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  background-color: white;
  box-shadow: 0 0 50px 0 grey;
  box-sizing: border-box;
  padding: 70px 30px 30px 30px;
}

.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  top: -50px;
  left: calc(50% - 50px);
}

h1 {
  margin: 0;
  padding: 0 0 20px;
  text-align: center;
  font-weight: bold;
  font-size: 22px;
}

.loginbox p {
  padding-left: 6px;
  margin-bottom: 0px;
  margin-top: 10px;
  font-weight: bold;
}

.loginbox input {
  width: 100%;
  margin-bottom: 20px;
}

.loginbox input[type="text"],
input[type="password"] {
  width: 95%;
  border: none;
  border-bottom: 1px solid rgb(70, 70, 70);
  padding: 5px;
  margin: 0px;
  outline: none;
}

.loginbox input[type="submit"] {
  margin-top: 20px;
  border: none;
  outline: none;
  height: 40px;
  background: #227ec9e8;
  color: #fff;
  font-size: 18px;
  border-radius: 20px;
}

.loginbox input[type="submit"]:hover {
  opacity: 0.5;
}

.loginbox a {
  text-decoration: none;
  font-size: 12px;
  line-height: 20px;
  color: darkgrey;
}

.loginbox a:hover {
  color: #ffc107;
}

